import classnames from 'classnames';
import IconSelect from '../../src/js/components/icon-select/index';
import { iconSize, paddingTypes, animationTypes } from './config';

import {
	RichText,
	BlockControls,
	AlignmentToolbar,
	InspectorControls,
	PanelColorSettings,
	withColors,
	FontSizePicker,
	withFontSizes,
	URLInput,
} from '@wordpress/block-editor';

import {
	Fragment,
	useCallback,
} from '@wordpress/element';

import {
	PanelBody,
	BaseControl,
	ToggleControl,
	TextControl,
	Button,
	RangeControl,
	RadioControl,
} from '@wordpress/components';

import {
	compose,
} from '@wordpress/compose';

import { __ } from '@wordpress/i18n';

const NEW_TAB_REL = 'noreferrer noopener';
const MIN_BORDER_RADIUS_VALUE = 0;
const MAX_BORDER_RADIUS_VALUE = 50;
const INITIAL_BORDER_RADIUS_POSITION = 4;

function ysButton( props ) {
	const {
		textColor,
		backgroundColor,
		setTextColor,
		setBackgroundColor,
		attributes,
		setAttributes,
		isSelected,
		fontSize,
		setFontSize,
	} = props;
	const {
		borderRadius,
		text,
		iconLeft,
		iconSizeLeft,
		iconRight,
		iconSizeRight,
		align,
		url,
		rel,
		linkTarget,
		paddingType,
		buttonType,
		maxWidth,
		maxUnit,
		animationType,
		animationInterval,
	} = attributes;

	const wrapClasses = classnames(
		'wp-block-button',
		{
			[ `has-text-align-${ align }` ]: align,
			[ fontSize.class ]: fontSize.class,
		}
	);

	const linkClasses = classnames(
		'wp-block-button__link',
		'ystdb-button__link',
		{
			[ textColor.class ]: textColor.class,
			'has-text-color': textColor.class,
			[ backgroundColor.class ]: backgroundColor.class,
			'has-background': backgroundColor.class,
			[ paddingType ]: paddingType,
			[ buttonType ]: buttonType,
			'has-animation': animationType,
			[ `has-animation--${ animationType }` ]: 'none' !== animationType,
		}
	);

	const wrapStyles = {
		fontSize: fontSize.size ? fontSize.size + 'px' : undefined,
	};
	const linkStyles = {
		color: textColor.color,
		backgroundColor: backgroundColor.color,
		borderRadius,
		maxWidth: buttonType && maxWidth ? `${ maxWidth }${ maxUnit }` : undefined,
		animationDuration: 'none' !== animationType && animationInterval ? `${ animationInterval }s` : undefined,
	};

	const onSetLinkRel = useCallback(
		( value ) => {
			setAttributes( { rel: value } );
		},
		[ setAttributes ]
	);

	const onToggleOpenInNewTab = useCallback(
		( value ) => {
			const newLinkTarget = value ? '_blank' : undefined;

			let updatedRel = rel;
			if ( newLinkTarget && ! rel ) {
				updatedRel = NEW_TAB_REL;
			} else if ( ! newLinkTarget && rel === NEW_TAB_REL ) {
				updatedRel = undefined;
			}

			setAttributes( {
				linkTarget: newLinkTarget,
				rel: updatedRel,
			} );
		},
		[ rel, setAttributes ]
	);

	const maxWidthUnitMaximum = 'px' === maxUnit ? 1200 : 100;
	const maxWidthValue = '%' === maxUnit && 100 < maxWidth ? 100 : maxWidth;

	return (
		<Fragment>
			<BlockControls>
				<AlignmentToolbar
					value={ align }
					onChange={ ( nextAlign ) => {
						setAttributes( { align: nextAlign } );
					} }
				/>
			</BlockControls>
			<InspectorControls>
				<PanelColorSettings
					title={ __( 'Color Settings' ) }
					initialOpen={ true }
					colorSettings={ [
						{
							value: backgroundColor.color,
							onChange: ( newColor ) => {
								setAttributes( { customGradient: undefined } );
								setBackgroundColor( newColor );
							},
							label: __( 'Background Color' ),
						},
						{
							value: textColor.color,
							onChange: ( color ) => {
								setTextColor( color );
							},
							label: __( 'Text Color' ),
						},
					] }
				>
				</PanelColorSettings>
				<PanelBody title={ __( '', 'ystandard-blocks' ) }>
					<RangeControl
						value={ borderRadius }
						label={ __( '', 'ystandard-blocks' ) }
						min={ MIN_BORDER_RADIUS_VALUE }
						max={ MAX_BORDER_RADIUS_VALUE }
						initialPosition={ INITIAL_BORDER_RADIUS_POSITION }
						allowReset
						onChange={ ( value ) => {
							setAttributes( { borderRadius: value } );
						} }
					/>
				</PanelBody>
				<PanelBody title={ __( '', 'ystandard-blocks' ) }>
					<IconSelect
						panelTitle={ __( '', 'ystandard-blocks' ) }
						iconControlTitle={ '' }
						selectedIcon={ iconLeft }
						onClickIcon={ ( value ) => {
							setAttributes( { iconLeft: value } );
						} }
					/>
					<BaseControl label={ __( '', 'ystandard-blocks' ) }>
						<div className={ 'ystdb-btn-selector components-base-control' }>
							{ iconSize.map( ( item ) => {
								return (
									<Button
										isDefault
										isPrimary={ iconSizeLeft === item.value }
										onClick={ () => {
											setAttributes( {
												iconSizeLeft: item.value,
											} );
										} }
									>
										<span>{ item.label }</span>
									</Button>
								);
							} ) }
						</div>
					</BaseControl>
					<IconSelect
						panelTitle={ __( '', 'ystandard-blocks' ) }
						iconControlTitle={ '' }
						selectedIcon={ iconRight }
						onClickIcon={ ( value ) => {
							setAttributes( { iconRight: value } );
						} }
					/>
					<BaseControl label={ __( '', 'ystandard-blocks' ) }>
						<div className={ 'ystdb-btn-selector components-base-control' }>
							{ iconSize.map( ( item ) => {
								return (
									<Button
										isDefault
										isPrimary={ iconSizeRight === item.value }
										onClick={ () => {
											setAttributes( {
												iconSizeRight: item.value,
											} );
										} }
									>
										<span>{ item.label }</span>
									</Button>
								);
							} ) }
						</div>
					</BaseControl>
				</PanelBody>

				<PanelBody title={ __( '', 'ystandard-blocks' ) }>
					<BaseControl>
						<FontSizePicker
							label={ __( '', 'ystandard-blocks' ) }
							value={ fontSize.size }
							onChange={ ( font ) => {
								setFontSize( font );
							} }
						/>
					</BaseControl>
				</PanelBody>
				<PanelBody title={ __( '', 'ystandard-blocks' ) }>
					<BaseControl>
						<span className={ `ystdb-info__small` }></span>
						<div className={ 'ystdb-btn-selector components-base-control' }>
							{ paddingTypes.map( ( item ) => {
								return (
									<Button
										isDefault
										isPrimary={ paddingType === item.value }
										onClick={ () => {
											setAttributes( {
												paddingType: item.value,
											} );
										} }
									>
										<span>{ item.label }</span>
									</Button>
								);
							} ) }
						</div>
					</BaseControl>
				</PanelBody>
				<PanelBody title={ __( '', 'ystandard-blocks' ) }>
					<BaseControl>
						<ToggleControl
							label={ __( '', 'ystandard-blocks' ) }
							onChange={ () => {
								const value = 'is-block' === buttonType ? '' : 'is-block';
								setAttributes( { buttonType: value } );
							} }
							checked={ buttonType === 'is-block' }
						/>
						<RangeControl
							value={ maxWidthValue }
							label={ __( '', 'ystandard-blocks' ) }
							min={ 0 }
							max={ maxWidthUnitMaximum }
							initialPosition={ 100 }
							allowReset
							onChange={ ( value ) => {
								setAttributes( { maxWidth: value } );
							} }
						/>
						<RadioControl
							label={ __( '', 'ystandard-blocks' ) }
							selected={ maxUnit }
							options={ [
								{ label: __( '%', 'ystandard-blocks' ), value: '%' },
								{ label: __( 'px', 'ystandard-blocks' ), value: 'px' },
							] }
							onChange={ ( option ) => {
								setAttributes( { maxUnit: option } );
							} }
						/>
					</BaseControl>
				</PanelBody>
				<PanelBody title={ __( '', 'ystandard-blocks' ) }>
					<BaseControl>
						<RadioControl
							label={ __( '', 'ystandard-blocks' ) }
							selected={ animationType }
							options={ animationTypes }
							onChange={ ( option ) => {
								setAttributes( { animationType: option } );
							} }
						/>
						<RangeControl
							value={ animationInterval }
							label={ __( '()', 'ystandard-blocks' ) }
							min={ 1 }
							max={ 10 }
							initialPosition={ 5 }
							allowReset
							onChange={ ( value ) => {
								setAttributes( { animationInterval: value } );
							} }
						/>
					</BaseControl>
				</PanelBody>
				<PanelBody title={ __( 'Link settings' ) }>
					<ToggleControl
						label={ __( 'Open in new tab' ) }
						onChange={ onToggleOpenInNewTab }
						checked={ linkTarget === '_blank' }
					/>
					<TextControl
						label={ __( 'Link rel' ) }
						value={ rel || '' }
						onChange={ onSetLinkRel }
					/>
				</PanelBody>
			</InspectorControls>

			<div className={ wrapClasses } style={ wrapStyles }>
				<span className={ linkClasses } style={ linkStyles }>
					<span className="ystdb-button__link-content">
						{ ( !! iconLeft &&
							<span className={ classnames(
								'ystdb-button__icon',
								'ystdb-button__icon--left',
								iconLeft,
								{
									[ iconSizeLeft ]: iconSizeLeft,
								}
							) }> </span>
						) }
						<RichText
							tagName={ 'span' }
							placeholder={ __( 'Add text' ) }
							value={ text }
							onChange={ ( value ) => setAttributes( { text: value } ) }
							withoutInteractiveFormatting
							className={ 'ystdb-button__text' }
						/>
						{ ( !! iconRight &&
							<span className={ classnames(
								'ystdb-button__icon',
								'ystdb-button__icon--right',
								iconRight,
								{
									[ iconSizeRight ]: iconSizeRight,
								}
							) }> </span>
						) }
					</span>
				</span>
			</div>

			{ ( !! isSelected &&
				<div className={ 'ystdb-button__link-container' }>
					<div className="ystdb-button__link-label">URL</div>
					<URLInput
						label={ __( 'Link' ) }
						className="ystdb-button__link"
						value={ url }
						/* eslint-disable jsx-a11y/no-autofocus */
						autoFocus={ false }
						/* eslint-enable jsx-a11y/no-autofocus */
						onChange={ ( value ) => setAttributes( { url: value } ) }
						disableSuggestions={ ! isSelected }
						isFullWidth
						hasBorder
					/>
				</div>
			) }
		</Fragment>
	);
}

export default compose( [
	withColors( 'backgroundColor', { textColor: 'color' } ),
	withFontSizes( 'fontSize' ),
] )( ysButton );
