import classnames from 'classnames';
import IconSelect from '../../../src/js/components/icon-select';
import { __ } from '@wordpress/i18n';

import {
	Fragment,
} from '@wordpress/element';
import { addFilter } from '@wordpress/hooks';
import {
	InspectorControls,
	FontSizePicker,
	getFontSizeClass,
} from '@wordpress/block-editor';
import { createHigherOrderComponent } from '@wordpress/compose';
import {
	PanelBody,
	ToggleControl,
	BaseControl,
	RadioControl,
} from '@wordpress/components';
import { select } from '@wordpress/data';
import {
	allowedBlocks,
} from './config';

/**
 * 
 */
const addBtnCustomControls = createHigherOrderComponent( ( BlockEdit ) => {
	return ( props ) => {
		if ( allowedBlocks.includes( props.name ) ) {
			const {
				attributes,
				setAttributes,
			} = props;
			const {
				buttonBlock,
				icon,
				customIcon,
				iconPosition,
				fontSize,
				buttonSize,
			} = attributes;
			const { fontSizes } = select( 'core/block-editor' ).getSettings();
			const selectedFontSize = fontSizes.find( ( item ) => item.size === fontSize );
			const fontSizeClass = selectedFontSize ? getFontSizeClass( selectedFontSize.slug ) : '';
			/**
			 * 
			 */
			const attrClassName = attributes.className ? attributes.className : '';
			/**
			 * 
			 *
			 * @param className
			 */
			const initClassName = ( className ) => {
				return className
					.replace( /\s?\-full\s?/g, '' )
					.replace( /has-.+-font-size/g, '' )
					.replace( /\s?\-sm\s?/g, '' )
					.replace( /\s?\-lg\s?/g, '' );
			};

			attributes.className = classnames(
				initClassName( attrClassName ),
				{
					'-full': buttonBlock,
					[ fontSizeClass ]: fontSizeClass,
					'has-custom-font-size': fontSizeClass,
					[ '-' + buttonSize ]: buttonSize,
				}
			);
			/**
			 * 
			 */
			const text = attributes.text ? attributes.text.replace( /<i.*class=".*?">.*?<\/i>/g, '' ) : '';
			let leftIcon = '';
			let rightIcon = '';
			let iconClass = icon;
			if ( iconClass ) {
				if ( 'custom' === iconClass ) {
					if ( customIcon ) {
						iconClass = customIcon;
					} else {
						iconClass = '';
					}
				}
			}
			const iconStyle = `margin-${ ( 'right' === iconPosition ? 'left' : 'right' ) }:.5rem;`;
			if ( iconClass ) {
				const iconHTML = `<i style="${ iconStyle }" class="${ iconClass }"> </i>`;
				leftIcon = 'left' === iconPosition ? iconHTML : '';
				rightIcon = 'right' === iconPosition ? iconHTML : '';
			}
			attributes.text = `${ leftIcon }${ text }${ rightIcon }`;

			if ( props.isSelected ) {
				return (
					<Fragment>
						<BlockEdit { ...props } />
						<InspectorControls>
							<PanelBody title={ __( '[ys]', 'ystandard-blocks' ) }>
								<div
									style={ { color: '#D53939', marginBottom: '.5rem' } }>[ys]
								</div>
								<div className={ 'ystdb-inspectorcontrols__disablecustomfont' }>
									<FontSizePicker
										label={ __( '', 'ystandard-blocks' ) }
										disableCustomFontSizes={ true }
										value={ fontSize }
										onChange={ ( newFontSize ) => {
											setAttributes( { fontSize: newFontSize } );
										} }
									/>
								</div>
							</PanelBody>
							<IconSelect
								iconPosition={ iconPosition }
								onChangePosition={ ( option ) => {
									setAttributes( { iconPosition: option } );
								} }
								selectedIcon={ iconClass }
								onClickIcon={ ( value ) => {
									setAttributes( { icon: value } );
								} }
								onClickClear={ () => {
									setAttributes( { icon: '' } );
								} }
								onChangeCustomIcon={ ( content ) => {
									setAttributes( { customIcon: content } );
									if ( content ) {
										setAttributes( { icon: 'custom' } );
									} else {
										setAttributes( { icon: '' } );
									}
								} }
								customIcon={ customIcon }
								customInfo={ '[ys]' }
								customInfoStyle={ { color: '#D53939', marginBottom: '.5rem' } }
							/>

							<PanelBody
								title={ __( '[ys]', 'ystandard-blocks' ) }
								initialOpen={ false }
							>
								<div style={ { color: '#D53939', marginBottom: '.5rem' } }>[ys]
								</div>
								<BaseControl>

									<ToggleControl
										label={ __( '', 'ystandard-blocks' ) }
										checked={ buttonBlock }
										onChange={ () => {
											setAttributes( {
												buttonBlock: ! buttonBlock,
											} );
										} }
									/>
								</BaseControl>
								<div className={ `ystdb-inspectorcontrols__horizontal-radio` }>
									<RadioControl
										label={ __( '', 'ystandard-blocks' ) }
										selected={ buttonSize }
										options={ [
											{ label: __( '', 'ystandard-blocks' ), value: '' },
											{ label: __( '', 'ystandard-blocks' ), value: 'lg' },
											{ label: __( '', 'ystandard-blocks' ), value: 'sm' },
										] }
										onChange={ ( option ) => {
											setAttributes( { buttonSize: option } );
										} }
									/>
								</div>
							</PanelBody>
						</InspectorControls>
					</Fragment>
				);
			}
		}
		return <BlockEdit { ...props } />;
	};
}, 'addBtnCustomControls' );

/**
 * 
 */
addFilter(
	'editor.BlockEdit',
	'ystdb/ex-btn-edit',
	addBtnCustomControls
);
