import IconSelect from '../../src/js/components/icon-select/index';

import {
	BlockControls,
	PlainText,
	AlignmentToolbar,
	InspectorControls,
	PanelColorSettings,
	ContrastChecker,
	withColors,
	FontSizePicker,
	withFontSizes,
} from '@wordpress/block-editor';

import {
	Fragment,
} from '@wordpress/element';

import {
	Disabled,
	PanelBody,
	BaseControl,
	ToggleControl,
	ServerSideRender,
	RadioControl,
} from '@wordpress/components';

import {
	withState,
	compose,
} from '@wordpress/compose';

import { __ } from '@wordpress/i18n';

const btnLinkEdit = ( props ) => {
	const {
		backgroundColor,
		textColor,
		setBackgroundColor,
		setTextColor,
		attributes,
		setAttributes,
		setState,
		isPreview,
		fontSize,
		setFontSize,
	} = props;
	const {
		content,
		icon,
		align,
		buttonType,
		iconPosition,
		buttonSize,
	} = attributes;

	return (
		<div className={ 'wp-block-html' }>
			<Fragment>
				<BlockControls>
					<AlignmentToolbar
						value={ align }
						onChange={ ( nextAlign ) => {
							setAttributes( { align: nextAlign } );
						} }
					/>
					<div className="components-toolbar">
						<button
							className={ `components-tab-button ${ ! isPreview ? 'is-active' : '' }` }
							onClick={ () => {
								setState( { isPreview: false } );
							} }
						>
							<span>HTML</span>
						</button>
						<button
							className={ `components-tab-button ${ isPreview ? 'is-active' : '' }` }
							onClick={ () => {
								setState( { isPreview: true } );
							} }
						>
							<span>{ __( 'Preview' ) }</span>
						</button>
					</div>
				</BlockControls>
				<Disabled.Consumer>
					{ () => (
						( isPreview ) ? (
							<div className={ 'ystdb-btn-link__preview' }>
								<ServerSideRender
									block="ystdb/btn-link"
									attributes={ attributes }
								/>
							</div>
						) : (
							<PlainText
								value={ content }
								onChange={ ( content ) => setAttributes( { content } ) }
								placeholder={ 'HTML...' }
								aria-label={ __( 'HTML' ) }
							/>
						)
					) }
				</Disabled.Consumer>
				<InspectorControls>
					<PanelColorSettings
						title={ __( 'Color Settings' ) }
						initialOpen={ true }
						colorSettings={ [
							{
								value: backgroundColor.color,
								onChange: ( color ) => {
									setBackgroundColor( color );
								},
								label: __( 'Background Color' ),
							},
							{
								value: textColor.color,
								onChange: ( color ) => {
									setTextColor( color );
								},
								label: __( 'Text Color' ),
							},
						] }
					>
						<ContrastChecker
							backgroundColor={ backgroundColor.color }
							textColor={ textColor.color }
						/>
					</PanelColorSettings>
					<PanelBody title={ __( '', 'ystandard-blocks' ) }>
						<FontSizePicker
							label={ __( '', 'ystandard-blocks' ) }
							value={ fontSize.size }
							onChange={ ( font ) => {
								setFontSize( font );
							} }
						/>
					</PanelBody>
					<IconSelect
						panelTitle={ __( '', 'ystandard-blocks' ) }
						iconPosition={ iconPosition }
						onChangePosition={ ( option ) => {
							setAttributes( { iconPosition: option } );
						} }
						selectedIcon={ icon }
						onClickIcon={ ( value ) => {
							setAttributes( { icon: value } );
						} }
					/>
					<PanelBody title={ __( '', 'ystandard-blocks' ) }>
						<BaseControl>
							<ToggleControl
								label={ __( '', 'ystandard-blocks' ) }
								checked={ ( buttonType === 'full' ) }
								onChange={ () => {
									setAttributes( {
										buttonType: buttonType === 'full' ? '' : 'full',
									} );
								} }
							/>
							<div className={ `ystdb-inspectorcontrols__horizontal-radio` }>
								<RadioControl
									label={ __( '', 'ystandard-blocks' ) }
									selected={ buttonSize }
									options={ [
										{ label: __( '', 'ystandard-blocks' ), value: '' },
										{ label: __( '', 'ystandard-blocks' ), value: 'lg' },
										{ label: __( '', 'ystandard-blocks' ), value: 'sm' },
									] }
									onChange={ ( option ) => {
										setAttributes( { buttonSize: option } );
									} }
								/>
							</div>
						</BaseControl>
					</PanelBody>

				</InspectorControls>
			</Fragment>
		</div>
	);
};

export default compose( [
	withColors( 'backgroundColor', { textColor: 'color' } ),
	withState( { isPreview: false, styles: '' } ),
	withFontSizes( 'fontSize' ),
] )( btnLinkEdit );
