import _ from 'lodash';
import allIcons from './icons.json';
import recommendIcons from './reccomend-icons.json';
import FontIconPicker from '@fonticonpicker/react-fonticonpicker';

import { __ } from '@wordpress/i18n';
import {
	BaseControl,
	PanelBody,
	RadioControl,
} from '@wordpress/components';

import {
	Component,
} from '@wordpress/element';

class IconSelect extends Component {
	render() {
		const {
			panelTitle,
			iconControlTitle,
			iconPosition,
			onChangePosition,
			selectedIcon,
			onClickIcon,
			customInfo,
			customInfoStyle,
		} = this.props;

		const controlPanelTitle = panelTitle ? panelTitle : __( '[ys]', 'ystandard-blocks' );
		const iconBaseControlTitle = iconControlTitle === undefined ? __( '', 'ystandard-blocks' ) : iconControlTitle;

		const icons = _.uniq( [ ...recommendIcons.icons, ...allIcons.icons ] );

		const pickerProps = {
			icons,
			theme: 'bluegrey',
			renderUsing: 'class',
			value: selectedIcon,
			onChange: ( value ) => {
				onClickIcon( value );
			},
			isMulti: false,
		};

		return (
			<PanelBody title={ controlPanelTitle }>
				{ ( customInfo &&
					<div style={ customInfoStyle }>{ customInfo }</div>
				) }
				{ ( !! onChangePosition &&
					<BaseControl label={ __( '', 'ystandard-blocks' ) }>
						<div className={ 'ystdb-icon-select__position' }>
							<RadioControl
								selected={ iconPosition }
								options={ [
									{ label: __( '', 'ystandard-blocks' ), value: 'left' },
									{ label: __( '', 'ystandard-blocks' ), value: 'right' },
								] }
								onChange={ onChangePosition }
							/>
						</div>
					</BaseControl>
				) }
				<BaseControl label={ iconBaseControlTitle }>
					<div className={ 'ystdb-icon-select__picker' }>
						<FontIconPicker { ...pickerProps } />
					</div>
				</BaseControl>
			</PanelBody>
		);
	}
}

export default IconSelect;
